// uni.request
import {
	request
} from '@dcloudio/uni-app'

// URL
const BASE_URL = 'https://api.xcbdsc.com'
// const BASE_URL = 'http://192.168.0.111:8088'

// const UPLOAD_URL = 'http://121.196.234.102:8080'
export const UPLOAD_URL = 'https://image.xcbdsc.com'

const WEBSOCKET_URL = "192.168.0.111:8088"


/**
 * 
 * @@param {url,parmas} 
 * @description websocket
*/
const getSocket  = (url,parmas)=>{
	let socketTask =  uni.connectSocket({
		url:`ws://${WEBSOCKET_URL}${url}?${parmas}`,
		 success(data) {
			console.log("websocket"+url);
		},
	})
	return socketTask
} 

export {getSocket}

// tokentoken
const whiteList = [
	// '/api/cglist',
	// '/api/citytree',
	// '/api/citylist',
	// '/api/marketlist',
	// '/group1/upload',
	// '/api/countrylist',
	// '/api/user/rs',
	// '/api/user/login',
	// '/api/user/sign',
	// '/api/market/commodity/list',
	// '/api/shop/list',
	// '/api/shop/lottery'
] // 

/**
 * token
 */
const checkToken = (url) => {
	let status = whiteList.includes(url)
	if (!status) {
		// token
		const token = uni.getStorageSync('token');
		return {
			'Content-Type': 'application/json',
			'Authorization': token
		}
	} else {
		return {
			'Content-Type': 'application/json'
		}
	}
}

// 
const fetch = (url, method, data = {}, headers = {}) => {
	return new Promise((resolve, reject) => {
		uni.request({
			url: `${BASE_URL}${url}`,
			method: method,
			data: data,
			header: checkToken(url),
			success: (res) => {
				const {
					code,
					msg
				} = res.data
				if (res.statusCode === 200 || code === 200) {
					resolve(res.data)
				} else if (res.statusCode === 201 || code === 201 || msg === '') {
					uni.showModal({
						title: '',
						content: '',
						confirmText: '',
						success: ({
							confirm
						}) => {
							if (confirm) {
								uni.navigateTo({
									url: '/pages/login/login',
								});
							}
						},
					})
				} else {
					// if (msg) {
					// 	uni.showModal({
					// 		title: '',
					// 		content: res.data.msg || res.data.message || res.data.error, //+res.data.error,
					// 		showCancel: false
					// 	})
					// }
					resolve(res.data)
				}
			},
			fail: (err) => {
				reject(err)
				console.log('error', err);
			}
		})
	})
}


